<template>
  <UploaderManyMedia
  :url="'news_media/'"
  :toAction="'addNews'"
  @done="handleUploadResult">
    <template #label>
      <svg fill="currentColor" viewBox="0 0 97.6 77.3" width="96"><title>Icon to represent media such as images or videos</title><path d="M16.3 24h.3c2.8-.2 4.9-2.6 4.8-5.4-.2-2.8-2.6-4.9-5.4-4.8s-4.9 2.6-4.8 5.4c.1 2.7 2.4 4.8 5.1 4.8zm-2.4-7.2c.5-.6 1.3-1 2.1-1h.2c1.7 0 3.1 1.4 3.1 3.1 0 1.7-1.4 3.1-3.1 3.1-1.7 0-3.1-1.4-3.1-3.1 0-.8.3-1.5.8-2.1z" fill="currentColor"></path><path d="M84.7 18.4 58 16.9l-.2-3c-.3-5.7-5.2-10.1-11-9.8L12.9 6c-5.7.3-10.1 5.3-9.8 11L5 51v.8c.7 5.2 5.1 9.1 10.3 9.1h.6l21.7-1.2v.6c-.3 5.7 4 10.7 9.8 11l34 2h.6c5.5 0 10.1-4.3 10.4-9.8l2-34c.4-5.8-4-10.7-9.7-11.1zM7.2 10.8C8.7 9.1 10.8 8.1 13 8l34-1.9c4.6-.3 8.6 3.3 8.9 7.9l.2 2.8-5.3-.3c-5.7-.3-10.7 4-11 9.8l-.6 9.5-9.5 10.7c-.2.3-.6.4-1 .5-.4 0-.7-.1-1-.4l-7.8-7c-1.4-1.3-3.5-1.1-4.8.3L7 49 5.2 17c-.2-2.3.6-4.5 2-6.2zm8.7 48c-4.3.2-8.1-2.8-8.8-7.1l9.4-10.5c.2-.3.6-.4 1-.5.4 0 .7.1 1 .4l7.8 7c.7.6 1.6.9 2.5.9.9 0 1.7-.5 2.3-1.1l7.8-8.8-1.1 18.6-21.9 1.1zm76.5-29.5-2 34c-.3 4.6-4.3 8.2-8.9 7.9l-34-2c-4.6-.3-8.2-4.3-7.9-8.9l2-34c.3-4.4 3.9-7.9 8.4-7.9h.5l34 2c4.7.3 8.2 4.3 7.9 8.9z" fill="currentColor"></path><path d="M78.2 41.6 61.3 30.5c-2.1-1.4-4.9-.8-6.2 1.3-.4.7-.7 1.4-.7 2.2l-1.2 20.1c-.1 2.5 1.7 4.6 4.2 4.8h.3c.7 0 1.4-.2 2-.5l18-9c2.2-1.1 3.1-3.8 2-6-.4-.7-.9-1.3-1.5-1.8zm-1.4 6-18 9c-.4.2-.8.3-1.3.3-.4 0-.9-.2-1.2-.4-.7-.5-1.2-1.3-1.1-2.2l1.2-20.1c.1-.9.6-1.7 1.4-2.1.8-.4 1.7-.3 2.5.1L77 43.3c1.2.8 1.5 2.3.7 3.4-.2.4-.5.7-.9.9z" fill="currentColor"></path></svg>
    </template>
  </UploaderManyMedia>
  <form @submit.prevent="submitForm" class="space-y-4 max-w-md mx-auto">
    <div>
      <label>توضیحات:</label>
      <textarea v-model="form.description" rows="4" required></textarea>
    </div>
    <div v-if="!rule">
      <label>دسته‌بندی:</label>
      <multiselect
        v-model="form.category_id"
        :options="categories"
        :multiple="true"
        track-by="id"
        label="title"
        placeholder="دسته‌بندی را انتخاب کنید"
        selectLabel="برای انتخاب این مورد، اینتر بزنید"
        selectedLabel="انتخاب شده"
        deselectLabel="برای حذف این مورد، کلیک کنید"
        noOptions="موردی برای انتخاب وجود ندارد"
        noResult="نتیجه‌ای با این جستجو پیدا نشد"
      />
    </div>
    <AddressSelector
      :loginCity="address"
      :model-value="form.user_address"
      @update="val => form.user_address = val"
      @loading="isAddressLoading = $event"
    />
    <button type="submit" :disabled="isSubmitDisabled">ثبت خبر</button>
  </form>
</template>

<script setup>
    import { ref, onMounted, computed } from 'vue'
    import { sendApi } from '@/utils/api'
    import UploaderManyMedia from '@/components/tooles/upload/UploaderManyMedia.vue'
    import Multiselect from 'vue-multiselect'
    import 'vue-multiselect/dist/vue-multiselect.min.css'
    import AddressSelector from '@/components/tooles/news/AddressSelector .vue'
    const categories = ref([])
    const address = ref('')
    const rule = ref(false)
    const isAddressLoading = ref(false)
    const form = ref({
        category_id: [],
        user_address: { type: 'city', value: '' },
        media_id: [],
        description: '',
    })
    const handleUploadResult = (uploadedData) => {
      form.value.media_id = uploadedData.map(item => item.id)
    }
    onMounted(async () => {
        const data = await sendApi({ control: 'news', action: 'add_data' })
        if (data.status === 'success') {
            rule.value = data.rule
            address.value = data.address
            if (rule.value) {
              categories.value = data.category ? [data.category] : []
              form.value.category_id = data.category ? [data.category] : []
            } else {
              categories.value = data.category ?? []
            }
        }
    })
    const isSubmitDisabled = computed(() => {
      const isDescriptionEmpty = !form.value.description.trim()
      const isCategoryInvalid = !rule.value && (!form.value.category_id || form.value.category_id.length === 0)
      const isLocationSelected = form.value.user_address?.type === 'location'
      const isAddressInvalid = isLocationSelected && (!form.value.user_address?.value || !form.value.user_address.value.address?.trim())
      const isStillLoading = isLocationSelected && isAddressLoading.value
      return isDescriptionEmpty || isCategoryInvalid || isAddressInvalid || isStillLoading
    })
    const submitForm = async () => {
        const finalData = {
            ...form.value,
            category_id: form.value.category_id.map(c => c.id),
        }
        try {
            const res = await sendApi({
                control: 'news',
                action: 'add_news',
                data: finalData,
            })
            if (res.status === 'success') {
                alert('خبر با موفقیت ثبت شد!')
            } else {
                alert('خطا در ثبت خبر: ' + res.message)
            }
        } catch (err) {
            alert('خطا در ارسال: ' + err.message)
        }
    }
</script>

<style scoped>
  form input,
  form select,
  form textarea {
      width: 100%;
      padding: 0.5rem;
      margin-top: 0.25rem;
  }
  button {
    padding: 0.5rem 1rem;
    background-color: #10b981;
    color: white;
    border: none;
    border-radius: 0.375rem;
    cursor: pointer;
    margin-top: 0.5rem;
    transition: background-color 0.2s ease, opacity 0.2s ease;
  }

  button:disabled {
    background-color: #9ca3af;
    cursor: not-allowed;
    opacity: 0.6;
  }

</style>
